    .h4.mt-5(data-aos="fade-right") Six sigma
    p.mt-5 Es un modelo de gestin, se conoce por sus siglas en ingls DMAIC, el objetivo es el mejoramiento de los procesos; cuya traduccin es asociada a las siguientes palabras:          
    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8 
        h4(data-aos="fade-right") Definir
        p(data-aos="fade-left") El objetivo del problema o defecto para validar, adems de los miembros del programa.
        .hr 

    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8 
        h4(data-aos="fade-right") Medir
        p(data-aos="fade-left") Entender el problema o defecto.
        .hr 


    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8 
        h4(data-aos="fade-right") Analizar
        p(data-aos="fade-left") Investigar causas reales del problema o defecto.
        .hr 


    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8 
        h4(data-aos="fade-right") Mejorar
        p(data-aos="fade-left") Determinar las mejoras.
        .hr 


    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8 
        h4(data-aos="fade-right") Controlar
        p(data-aos="fade-left") Tomar medidas para mantener el progreso, valorando en trminos econmicos y el nivel de satisfaccin al cliente.
        .hr 

    p.mt-5 El nombre proviene en los casos en que se presenta una menor variacin entre los datos y as mismo es la desviacin estndar es menor, y por lo tanto mayor va a ser la sigma alcanzando el nivel 6. Six sigma sigue un modelo llamado ciclo DMAIC (Definir, Medir, Analizar, Mejorar y Controlar) lo cual es verificable en la figura 21.

