        p.mb-0 El control de los procesos es importante comprender cuales son las variaciones en las caractersticas del producto, encontrar las causas y tomar las decisiones para controlar la variabilidad, eliminando los errores, estandarizando el proceso y establecer mejoras para tener una variabilidad controlada.    
