        p En el proceso de patronaje la persona responsable del patronaje de la prenda, junto con el auditor de calidad realizan la revisin del paquete de moldes o patrones que comprenden la pieza, es importante revisarlos en fsico, aunque se desarrolle el patrn digital.

        p.mb-0 Los productos tienen variables y atributos. Las variables son caractersticas cuantitativas y los atributos son las caractersticas cualitativas. Los requisitos adems estn clasificados de la siguiente manera:

