    p.mt-5 En los puntos de revisin se analizan piezas y prendas terminadas, registrando en los formatos de producto terminado y producto en proceso. Este anlisis es directo ya que se relaciona con algo tangible, no menos importante se deben tener presente otros factores indirectos como son:
