            p.mt-4.mb-0 En consecuencia, los factores directos o indirectos si son mal ejecutados afectan la calidad y rendimiento de la empresa.

    p.mt-5 Existen otras tcnicas de anlisis como son las pruebas y ensayos en laboratorios especializados que evalan la prenda segn normas nacionales e internacionales; por ejemplo, la prueba de uso en la que se compara los resultados planteados y los reales obtenidos.

    p En estos ensayos o pruebas se analizan las fibras, en cuanto a la cantidad, colorimtrica, peso, composicin, cambios dimensionales despus del lavado, resistencia a la rotura y al desgarre, elongacin y elasticidad de la tela, recuperacin a las arrugas, resistencia a la abrasin, solidez al color con el frote, al agua, al planchado, resistencia a la formacin de motas (#[i Pilling]), entre otras.

    p En el siguiente cuadro podemos observar algunos ensayos con la norma relacionada que se puede realizar a la prenda. 

