    .h4.mt-5(data-aos="fade-right") Control de calidad en terminados            

    p.mt-5 En los terminados se incluyen procesos de planchado, empaque, embalaje y procesos de acabados como lavandera, estampacin y bordado. Puede ser que los procesos de acabados pueden desarrollarse de manera externa a la empresa, es decir contratar los servicios, y por lo tanto la importancia de los puntos de control una vez ingresen a la planta, validar las prendas o piezas una vez se realicen los procesos. 

    img.img-t.img-a.my-2(data-aos="zoom-in")(src='@/assets/ilustraciones/img57.png', alt='') 

    p.mt-5 El planchado es la ltima operacin del proceso de produccin, es necesario que las personas responsables de la seccin tengan los criterios claros para evaluar las prendas que pasarn a las operaciones para la entrega al cliente.

    p.mt-5 Los requisitos para revisar en cada una de las actividades del proceso de acabados son los siguientes: 

    .row.justify-content-center.align-items-center.mt-5
